@import 'variables/colors.scss';
@import 'variables/general.scss' ;

// section - Contact

.contact{
    display: flex;
    justify-content: center;
    background-color: $lightcolor;
}
.contact>div{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $lightcolor ;
    width: 80%;

    form {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
    }
}

input {
    width: 47%;
    height: 6vh;
    border: none;
    border-bottom: $darkcolor 2px solid;
    padding: 6vh 0; 
    margin-right: 2em;
    background-color: transparent;  
    color: #1d1c1f;
    font-family: 'Roboto Condensed', sans-serif;

    &:focus, input:focus{
        outline: none;
    }

    &::placeholder {
        color:$darkcolor;
        font-family: 'Roboto Condensed', sans-serif;
    }
}

label{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;

    h3{
        align-self: flex-start;
        color: $darkcolor;
        font-family: 'Roboto Condensed', sans-serif;
    }
}


textarea {
    width:100%;
    height: 20vh;
    border-radius: 0.5em;
    -webkit-border-radius: 0.5em;
    -moz-border-radius: 0.5em;
    -ms-border-radius: 0.5em;
    -o-border-radius: 0.5em;
}

.contact button {
    width: 100%;
    margin: 1.5em 0;
    padding: 1em 0;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1em;
    background-color: $darkcolor;
    opacity: 0.7;
    color: $lightcolor;
    border-radius: 7%;
    -webkit-border-radius: 7%;
    -moz-border-radius: 7%;
    -ms-border-radius: 7%;
    -o-border-radius: 7%;
    cursor: pointer;
    border: none;
    outline: none;
    cursor: pointer;

    &:hover{
        opacity: 1;
    }

    &:focus {
        outline: none;
    }

}